body {
  margin: 0;
  background-color: #111;
  color: #eee;
  font-family: Arial, sans-serif;
}
.dashboard {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
  padding: 2rem;
}
.tile {
  background: #222;
  padding: 1.5rem;
  border-radius: 12px;
  text-align: center;
  cursor: pointer;
  transition: 0.2s;
}
.tile:hover {
  background: #333;
}